@tailwind base;
@tailwind components;
@tailwind utilities;

/* Основные стили */
body {
  @apply antialiased;
}

/* Стили для книги */
.book-container {
  @apply min-h-screen bg-gray-50 dark:bg-gray-900 transition-colors;
}

/* Стили для обложки */
.cover {
  @apply h-full flex items-center justify-center cursor-pointer bg-gradient-to-br from-blue-200 via-white to-blue-100 dark:from-gray-900 dark:via-gray-800 dark:to-gray-900;
}

.cover h1 {
  @apply text-5xl font-extrabold text-center text-gray-900 dark:text-white drop-shadow-lg;
}

/* Стили для страницы */
.page {
  @apply flex-1 p-6 relative overflow-y-auto bg-white dark:bg-gray-800;
}

.page.flipping {
  @apply transform transition-transform;
  transition-duration: 600ms;
  transform: rotateY(180deg);
}

/* Стили для редактора */
.ql-editor {
  @apply min-h-[200px] bg-white dark:bg-gray-800 text-gray-900 dark:text-white;
}

.ql-toolbar {
  @apply border-t-2 border-l-2 border-r-2 border-gray-300 dark:border-gray-600 rounded-t-lg bg-white dark:bg-gray-800;
}

.ql-container {
  @apply border-2 border-gray-300 dark:border-gray-600 rounded-b-lg bg-white dark:bg-gray-800;
}

/* Стили для кнопок */
button {
  @apply px-4 py-2 rounded-lg transition-colors font-medium;
}

.btn-primary {
  @apply bg-blue-700 text-white hover:bg-blue-800 dark:bg-blue-400 dark:hover:bg-blue-500 font-bold;
}

.btn-secondary {
  @apply bg-gray-700 text-white hover:bg-gray-800 dark:bg-gray-400 dark:hover:bg-gray-500 font-bold;
}

/* Стили для модальных окон */
.modal {
  @apply fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50;
}

.modal-content {
  @apply bg-white dark:bg-gray-800 rounded-xl shadow-2xl p-6 min-w-[300px] max-w-[90%] max-h-[80vh] overflow-y-auto;
}

/* Стили для оглавления */
.toc {
  @apply fixed top-0 right-0 w-[300px] h-screen bg-white dark:bg-gray-800 shadow-lg transform translate-x-full transition-transform z-40;
}

.toc.visible {
  @apply translate-x-0;
}

.toc-content {
  @apply p-6 text-gray-900 dark:text-white h-full overflow-y-auto;
}

.toc-close-button {
  @apply hidden;
  z-index: 100;
}

.toc-content .toc-page-link {
  @apply block text-sm text-blue-600 dark:text-blue-400 hover:underline mb-1 bg-transparent px-0 py-0 rounded-none font-normal shadow-none border-none;
}

.toc-section {
  @apply mb-4;
}

.toc-section-header {
  @apply flex items-center justify-between cursor-pointer hover:bg-gray-100 dark:hover:bg-gray-700 p-2 rounded-lg;
}

.toc-section-content {
  @apply ml-4;
}

.toc-chapter {
  @apply mb-2;
}

.toc-chapter-header {
  @apply flex items-center justify-between cursor-pointer hover:bg-gray-100 dark:hover:bg-gray-700 p-2 rounded-lg;
}

.toc-chapter-content {
  @apply ml-4;
}

.toc-edit-input {
  @apply w-full px-2 py-1 border rounded-lg bg-white dark:bg-gray-700 text-gray-900 dark:text-white;
}

.toc-edit-buttons {
  @apply flex gap-2 mt-2;
}

/* Стили для закладок */
.bookmark {
  @apply absolute top-4 right-4 text-yellow-500 cursor-pointer z-10;
}

/* Стили для поиска */
.search-container {
  @apply p-4 bg-gray-100 dark:bg-gray-800 border-b border-gray-200 dark:border-gray-700;
}

.search-input {
  @apply w-full px-4 py-2 rounded-lg border border-gray-300 dark:border-gray-600 bg-white dark:bg-gray-700 text-gray-900 dark:text-white;
}

/* Стили для кнопок управления */
.controls {
  @apply fixed top-4 right-4 flex gap-2 z-20;
}

.control-button {
  @apply p-2 rounded-lg bg-white dark:bg-gray-800 shadow-lg text-gray-900 dark:text-white hover:bg-gray-100 dark:hover:bg-gray-700;
}

/* Стили для текста */
.prose {
  @apply text-gray-900 dark:text-white;
}

.prose h1, .prose h2, .prose h3, .prose h4, .prose h5, .prose h6 {
  @apply text-gray-900 dark:text-white;
}

.prose p {
  @apply text-gray-800 dark:text-gray-200;
}

/* Адаптивные стили */
@media (max-width: 768px) {
  .toc {
    @apply w-full;
  }
  
  .toc-close-button {
    @apply block fixed top-4 left-4 z-50 p-2 rounded-lg bg-white dark:bg-gray-800 shadow-lg text-gray-900 dark:text-white hover:bg-gray-100 dark:hover:bg-gray-700;
  }
  
  .modal-content {
    @apply w-[90%] mx-4;
  }
  
  .controls {
    @apply top-2 right-2;
  }
}

/* Дополнительные стили */
.dark .ql-toolbar {
  background-color: #1f2937;
  border-color: #374151;
}

.dark .ql-container {
  background-color: #111827;
  border-color: #374151;
}

.dark .ql-editor {
  color: #e5e7eb;
}

.dark .ql-picker {
  color: #e5e7eb;
}

.dark .ql-stroke {
  stroke: #e5e7eb;
}

.dark .ql-fill {
  fill: #e5e7eb;
}

.dark .ql-picker-options {
  background-color: #1f2937;
  border-color: #374151;
} 